import axios from 'axios';
import {api} from './api';
import {setArticles,setCode,setUrl} from '../actions';
import {store} from '../reducers';

function getArticles(url,page=1){
    store.dispatch(setCode(100));
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    axios.get(api(url),{params: {page: page}})
          .then((response) => {
                if(response.data.code===200){
                    store.dispatch(setArticles(response.data.articles,response.data.title));
                    store.dispatch(setUrl(url));
                }else{
                    store.dispatch(setCode(404));
                }

          })
          .catch((error) => {
                console.log(error);
          });
}

export {getArticles}



